.container-fluid {
    padding-top: 6vh; 
    padding-bottom: 10vh; 
}

.row {
    padding: 0; 
    margin-left: 25px;
    margin-right: 25px; 
}

.about-row {
    height: 70vh; 
}

.about-information {
    padding-left: 40px; 
    display: flex;
    align-items: center;
}

.about-intro {
    margin-top: 20px; 
    margin-bottom: 7.5vh; 
    position: relative; 
    font-size: 2vw; 
    font-family: "Space Mono Regular"; 
    display: inline-block; 
    color: #1a1a1a
}

.about-intro::before,
.about-intro::after {
    content: '';
    position: absolute; 
    top: 0; 
    right: 0; 
    bottom: 0; 
    left: 0; 
}

.about-intro::before {
    background: #FAF9F6; 
    -webkit-animation: typewriter 1.5s steps(25) 0.5s forwards; 
            animation: typewriter 1.5s steps(25) 0.5s forwards; 
}

.about-intro::after {
    width: 0.125em; 
    background: #1a1a1a;
    -webkit-animation: typewriter 1.5s steps(25) 0.5s forwards, blink 700ms steps(25) infinite;
            animation: typewriter 1.5s steps(25) 0.5s forwards, blink 700ms steps(25) infinite; ; 
}

@-webkit-keyframes typewriter {
    to { 
        left: 100% 
    }
}

@keyframes typewriter {
    to { 
        left: 100% 
    }
}

@-webkit-keyframes blink {
    to {
        background: transparent
    }
}

@keyframes blink {
    to {
        background: transparent
    }
}

.about-info {
    font-family: 'Helvetica Neue Light';
    font-size: 1.3vw;  
    width: 90%; 
    margin-bottom: 7.5vh; 
} 

